// import tabs from "@/components/tabs";
// import pagetitle from "@/components/title";
import {getCart,getProd} from '../../common/fetch.js'
export default {
  name: "App",
  // components: {
  //   tabs,
  //   pagetitle
  // },
  
  data(){
    return {
      CartList:[],
	  prodList: [],
      handleShow: true,
      checked: false,
	  total_count: 0,
	  total_price: 0,
	  Users_ID:'wkbq6nc2kc',
	  User_ID:3,
	  prod_arg: {
		page: 1,
		pageSize: 4,
	  },
	  hasMore: true, // 是否还有产品
    }
  },
  // 用户下拉
  onPullDownRefresh() {
  	
  },
  // 上拉触底
  onReachBottom() {
  	if(this.hasMore) {
		this.getProd();
	}
  },
  created() {
  	this.getCart();
	this.getProd();
  },
  methods: {
    handle(){
      this.handleShow = !this.handleShow;            
    },
	getCart() {
		getCart({Users_ID:this.Users_ID,User_ID: this.User_ID,cart_key:'CartList'}).then(res=>{
			console.log(res)
			if(res.errorCode == 0){
				this.CartList = res.data.CartList;
				this.total_count= res.data.total_count;
				this.total_price= res.data.total_price;				
			}
		}).catch(e=>console.log(e))
	},
	getProd(){
		this.prod_arg.Users_ID = this.Users_ID;
		let oldlist = this.prodList;
		getProd(this.prod_arg).then(res=>{
			console.log(res)
			if(res.errorCode == 0){
				this.prodList = oldlist.concat(res.data);
				this.hasMore = (res.totalCount / this.prod_arg.pageSize) > this.prod_arg.page ? true : false ;
				this.prod_arg.page += 1;				
			}
		}).catch(e=>console.log(e))		
	}
  },
  computed: {
    manage(){
      return this.CartList.length == 0
    },
	
  }
};
